import React, { Component } from 'react';
import { Navbar, NavbarBrand, NavbarToggler, Nav, NavItem, NavLink, ButtonGroup, Button, Collapse } from 'reactstrap';

import ComponentHomeTop from './components/home/top.jsx';
import ComponentHomeBottom from './components/home/bottom.jsx';
import ComponentGenericPageTop from './components/genericpage/top.jsx';
import ComponentGenericPageBottom from './components/genericpage/bottom.jsx';

class App extends Component
{
    constructor(props)
    {
        super(props);
        this._render_navigation = this._render_navigation.bind(this);
        this._event_onPop = this._event_onPop.bind(this);
        this.switchToComponent = this.switchToComponent.bind(this);
        window.dispatchEvent(new CustomEvent("_event_onGetBase",
            {
                detail:
                {
                    callback: (e) =>
                    {
                        this.BaseURL = e;
                    }
                }
            }));
        window.dispatchEvent(new CustomEvent("_event_onRequestFile",
            {
                detail:
                {
                    path: this.BaseURL + "/resources/navigation.json",
                    onLoaded: (navigation) =>
                    {
                        navigation = JSON.parse(navigation);
                        this.NavigationComponents = {};
                        this.NavigationDirectory = {};
                        this.NavigationRouting = {};
                        this.NavigationTitles = {};
                        Object.keys(navigation.links).forEach((key) =>
                        {
                            this.NavigationTitles[key.toLowerCase()] = key;
                            this.NavigationComponents[key.toLowerCase()] = navigation.links[key].component;
                            this.NavigationRouting[key.toLowerCase()] = navigation.links[key].routing;
                            this.NavigationDirectory[key.toLowerCase()] = navigation.links[key].directory;
                        });
                        Object.keys(navigation.buttons).forEach((key) =>
                        {
                            this.NavigationComponents[key.toLowerCase()] = navigation.buttons[key][2];
                            if (navigation.buttons[key][3])
                            {
                                this.NavigationDirectory[key.toLowerCase()] = navigation.buttons[key][3];
                            }
                        });
                        this.setState({
                            title: navigation.title,
                            href: navigation.href,
                            links: navigation.links,
                            buttons: navigation.buttons,
                            default: navigation.default,
                            navbar: navigation.navbar,
                            titleTree: navigation.titleTree,
                            firstColor: navigation.navbar.color
                        });
                        if (window.location.pathname === this.BaseURL || window.location.pathname === this.BaseURL + "/" || window.location.pathname === "/")
                        {
                            this.switchToComponent(navigation.default);
                        }
                        else
                        {
                            let p = window.location.pathname;
                            p = p.substring(this.BaseURL.length);
                            let found = false;
                            Object.keys(this.NavigationRouting).forEach((key) =>
                            {
                                if (this.NavigationRouting[key] === p)
                                {
                                    this.switchToComponent(key);
                                    found = true;
                                }
                            });
                            if (!found)
                            {
                                this.switchTo404();
                            }    
                        }
                    }
                }
            }));
        this.initial = true;
        window.onpopstate = this._event_onPop;
    }
    _event_onPop(e)
    {
        this.initial = true;
        if (window.location.pathname === this.BaseURL || window.location.pathname === this.BaseURL + "/" || window.location.pathname === "/")
        {
            this.switchToComponent(this.state.default);
        }
        else
        {
            let p = window.location.pathname;
            p = p.substring(this.BaseURL.length);
            let found = false;
            Object.keys(this.NavigationRouting).forEach((key) =>
            {
                if (this.NavigationRouting[key] === p)
                {
                    this.switchToComponent(key);
                    found = true;
                }
            });
            if (!found)
            {
                this.switchTo404();
            }    
        }
    }
    componentWillMount()
    {
        this.setState({
            title: "",
            href: "",
            links: {},
            buttons: {},
            navbar: {},
            selected: "",
            selectedComponent: "",
            firstColor: "rgb(20, 21, 22)",
            titleTree: "",
            isOpen: false
        });
    }
    switchToComponent(activeKey)
    {
        activeKey = activeKey.toLowerCase();
        let componentName = this.NavigationComponents[activeKey];
        this.setState({
            selected: activeKey,
            selectedComponent: componentName
        });
        let componentLink = this.NavigationRouting[activeKey];
        if (!window.location.pathname.includes(componentLink))
        {
            if (this.initial)
            {
                window.history.replaceState({}, "", this.BaseURL + componentLink);
                this.initial = false;
            }
            else
            {
                window.history.pushState({}, "", this.BaseURL + componentLink);
            }
        }
        else
        {
            if (this.replace)
            {
                window.history.pushState({}, "", this.BaseURL + componentLink);
                this.replace = false;
            }
        }
        document.title = this.state.titleTree + this.NavigationTitles[activeKey];
    }
    switchTo404()
    {
        this.setState({
            selected: "_INTERNAL_404_",
            selectedComponent: "404"
        });
        window.history.replaceState({}, "", this.BaseURL + "/404");
        document.title = this.state.titleTree + "Page Not Found";
    }
    _render_navigation()
    {
        let items = {
            left: [],
            right: []
        };
        let buttons = [];
        Object.keys(this.state.links).forEach((key) =>
        {
            let hrefType = this.state.links[key].component;
            let href = "#";
            if (hrefType === "link")
            {
                href = this.state.links[key].routing;
            }
            let clickLinkHandler = () =>
            {
                if (hrefType !== "link")
                {
                    this.replace = true;
                    this.switchToComponent(key);
                }    
            };
            try
            {
                if (key.toLowerCase() === this.state.selected)
                {
                    items[this.state.links[key].visible].push(<NavItem><NavLink href={href} onClick={clickLinkHandler} active>{key}</NavLink></NavItem>);
                }
                else
                {
                    items[this.state.links[key].visible].push(<NavItem><NavLink href={href} onClick={clickLinkHandler}>{key}</NavLink></NavItem>);
                }
            }
            catch (e)
            {

            }
        });
        Object.keys(this.state.buttons).forEach((key) =>
        {
            let hrefType = this.state.buttons[key][1];
            let clickButtonHandler = () =>
            {
                if (hrefType === "component")
                {
                    this.switchToComponent(key);
                }
                else if (hrefType === "link")
                {
                    window.location = this.state.buttons[key][2];
                }
            };
            buttons.push(<Button outline color={this.state.buttons[key][0]} onClick={clickButtonHandler}>{key}</Button>);
        });
        return (
            <Navbar style={{ backgroundColor: this.state.navbar.color }} inverse toggleable fixed="bottom" className="sticky-top">
                <NavbarBrand href={this.state.href} style={{ color: "rgba(250, 250, 250, 0.75)" }} dangerouslySetInnerHTML={{ __html: this.state.title }}></NavbarBrand>
                <NavbarToggler style={{ position: "absolute", right: 10 }} onClick={(e) => (this.setState({ isOpen: !this.state.isOpen }) )} />
                <Collapse isOpen={this.state.isOpen} navbar>
                    <Nav className="mr-auto" navbar>
                        {items["left"]}
                    </Nav>
                    <Nav className="ml-auto" navbar>
                        {items["right"]}
                        <ButtonGroup>
                            {buttons}
                        </ButtonGroup>
                    </Nav>
                </Collapse>    
            </Navbar>
        );
    }
    componentTop()
    {
        let args = "";
        if (this.NavigationDirectory && this.NavigationDirectory[this.state.selected])
        {
            args = this.NavigationDirectory[this.state.selected];
        }
        return {
            "home": <ComponentHomeTop />,
            "generic": <ComponentGenericPageTop args={args} />,
            "404": <ComponentGenericPageTop args={this.BaseURL + "/resources/404/"} />
        };
    }
    componentBottom()
    {
        let args = "";
        if (this.NavigationDirectory && this.NavigationDirectory[this.state.selected])
        {
            args = this.NavigationDirectory[this.state.selected];
        }
        return {
            "home": <ComponentHomeBottom />,
            "generic": <ComponentGenericPageBottom args={args} firstColor={this.state.firstColor} />,
            "404": <ComponentGenericPageBottom args={this.BaseURL + "/resources/404/"} firstColor={this.state.firstColor} />
        };
    }
    render()
    {
        return (
            <div className="App">
                {this.componentTop()[this.state.selectedComponent]}
                {this._render_navigation()}
                {this.componentBottom()[this.state.selectedComponent]}
            </div>
        );
    }
}
export default App;


